.products-page {
    height: -webkit-max-content;
    height: max-content;
    margin: auto;
}

.page-heading{
    margin: auto;
}
.page-heading-title {
    font-size: 64px;
    color: #FF3636;
}

.page-heading-line {
    background: white;
    height: 10px;
    width: 146px;
    border-radius: 5px;
    margin: auto;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.product-card-container {
    max-width: 1076px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 10px;
    justify-content: space-around;
    display: grid;
    margin: 100px auto;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    -webkit-border-radius: 10px 10px 0px 0px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.product-card {
    position: relative;
    margin-bottom: 46px;
    background: #FF3636;
    width: 345px;
    height: 398px;
    color: #fff;
    font-size: 20px;
    align-content: center;
    text-align: center;
    border: #FF3636 21px solid;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.product-card-contents {
    margin: auto;
}

.product-card-img {
    background: black;
    border: solid black;
    border-radius: 50%;
    padding: 10px;
    width: 206px;
    height: 196px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.product-card-price {
    font-family: Montserrat;
    font-weight: 600;
    font-size: 32px;
    text-align: right;
}

.product-card-name {
    font-family: Playball;
    font-size: 36px;
    text-align: center;
    margin: auto;
}

.product-card-buy-button {
    background:black;
    width: 250px;
    height: 59px;
    padding-top: 12px;
    margin: auto;
    border-radius: 30px;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    -ms-border-radius: 30px;
    -o-border-radius: 30px;
}
.product-card-buy-button-text {
    margin: auto;
    font-family: Montserrat;
    font-weight: 700;
    font-size: 24px;

}
.contact-page {
    padding: 0 4em;
    margin: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form-group__label{
    color:black;
}
.contact-page__header {
    color: #FF3636;
    font-size: 4em;
    padding: 1em;
}

.contact-page__form-wrapper {
    display: flex;
    grid-gap: 9em;
    gap: 9em;
    margin-top: 2em;
    padding: 4em;
    padding-right: 6em;
    background: white;
    border: 6px solid black;
    border-radius: 30px;
    font-family: "Montserrat", sans-serif;
}

.contact-page__form-left {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 4em;
    gap: 4em;
    min-height: 100%;
}

.contact-page__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8em;
    gap: 0.8em;
    width: 500px;
}

.form-group {
    display: flex;
    flex-direction: column;
    grid-gap: 0.5em;
    gap: 0.5em;
    width: 100%;
}

.form-group__label {
    font-size: 1.1em;
    font-family: "Montserrat", sans-serif;
}

.form-group__input {
    border: 1px solid black;
    border-radius: 10px;
    padding: 1.5em;
    font-size: 1em;
}

.form-group__textarea {
    border: 1px solid black;
    border-radius: 10px;
    padding: 1.5em;
    font-size: 1em;
    height: 140px;
}

.contact-page__form__submit {
    background: #FF3636;
    color: white;
    padding: 1em;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 1.4em;
    border: none;
    border-radius: 30px;
    width: 100%;
    margin-top: 0.5em;
}

@media screen and (max-width: 1024px) {
    .contact-page__form-wrapper {
        display: flex;
        grid-gap: 6em;
        gap: 6em;
        margin-top: 2em;
        padding: 2em 3em;
    }

    .contact-page__form {
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 0.8em;
        gap: 0.8em;
        width: 400px;
    }
}

@media screen and (max-width: 900px) {
    .contact-page__form-wrapper {
        display: flex;
        grid-gap: 3em;
        gap: 3em;
        margin-top: 2em;
        padding: 2em 3em;
    }

    .contact-page__form-left__logo {
        width: 160px;
    }

    .contact-image {
        width: 160px;
    }

    .contact-page__form {
        grid-gap: 0.8em;
        gap: 0.8em;
        width: 280px;
    }

    .form-group__label {
        font-size: 0.8em;
    }

    .form-group__input {
        padding: 0.75em;
        font-size: 1em;
    }

    .form-group__textarea {
        padding: 0.75em;
        font-size: 1em;
        height: 100px;
    }

    .contact-page__form__submit {
        padding: 1.2em;
        font-size: 0.8em;
    }
}

@media screen and (max-width: 600px) {
    .contact-page {
        padding: 2em 2em;
        margin: 0;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .contact-page__header {
        color: #FF3636;
        font-family: sans-serif;
        font-size: 2.8em;
        padding: 1em 0;
    }

    .contact-page__form-wrapper {
        flex-direction: column;
        grid-gap: 2em;
        gap: 2em;
        margin-top: 2em;
        padding: 2em 1.5em;
    }

    .contact-page__form-left {
        flex-direction: row;
        align-items: center;
        grid-gap: 2em;
        gap: 2em;
        min-height: 100%;
    }

    .contact-page__form-left__logo {
        width: 160px;
    }

    .contact-image {
        width: 80px;
    }

    .contact-page__form {
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 0.5em;
        gap: 0.5em;
        width: 100%;
    }

    .form-group {
        display: flex;
        flex-direction: column;
        grid-gap: 0.2em;
        gap: 0.2em;
        width: 100%;
    }

    .form-group__label {
        font-size: 1em;
        font-family: "Montserrat", sans-serif;
    }

    .form-group__input {
        padding: 1em;
        font-size: 1em;
    }

    .form-group__textarea {
        border: 1px solid black;
        border-radius: 10px;
        padding: 1em;
        font-size: 1em;
        height: 140px;
    }

    .contact-page__form__submit {
        padding: 1em;
        font-size: 1em;
    }
}
/* ===========Container=========== */
.container {
    padding: 20px 10%;
    height: -webkit-max-content;
    height: max-content;
}

/* ===========HEADER=========== */

header {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 10%;
    font-family: Montserrat, sans-serif;
}

header ul {
    color: white;
    list-style: none;
    display: flex;
    align-items: center;
    margin-top: 20px;
}

header ul a {
    padding: 10px 25px;
    margin-left: 20px;
    text-decoration: none;
    transition: all 0.3s linear;
    color: #fff;
    font-weight: 500;
    font-size: 14px;
    border-radius: 25px;
}

header ul a:hover {
    color: #fff;
    background-color: #FF3636;
    transition: all 0.3s linear;
}

/* ===========FOOTER=========== */
footer {
    background-color: #FF3636;
    margin-top: 20px;
    padding: 50px 10%;
    border-top: 5px solid #000;
}

footer>img {
    color: #fff;
}

.footer {
    font-family: Montserrat;
    display: flex;
    text-align: left;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 50px;
}

.footer h2 {
    color: #fff;
}

.footer-links a {
    font-weight: 500;
    padding: 10px;
    text-decoration: none;
    transition: all 0.3s linear;
    margin-right: 20px;
    color: #333;
}

.footer-links a:hover {
    color: #fff;
}

.footer-links div {
    margin: 30px 0;
    ;
}

.footer-contact {
    text-align: right;
}

.footer-contact h2 {
    margin-bottom: 15px;
}

.footer-contact p {
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 5px;
}

.footer-socials {

    margin-top: 20px;
}

.footer-socials img {
    height: 30px;
    cursor: pointer;
    margin-left: 20px;
}

#top {
    text-align: left;
    height: 50px;
    margin-top: 50px;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    header ul a {
        padding: 10px 15px;
        margin-left: 0px;
    }
}

@media screen and (max-width: 375px) {
    header {
        flex-direction: column;
    }

    header ul a {
        padding: 10px;
        margin-left: 0px;
    }

    .footer {
        flex-direction: column;
    }

    .footer-links {
        margin: 10px 0;
    }

    .footer-links img {
        display: none;
    }

    .footer-contact {
        text-align: left;
    }

    .footer-socials img {
        height: 20px;
        margin-right: 20px;
        margin-left: 0px;
    }
}
